import React from 'react';
import styled, { css } from 'styled-components';

class SingleMenuItem extends React.Component {
	render() {
		return (
  <div>
    <Holder>
      <SingleItemDiv>
        <SingleItemDetails>
          <SingleItemDetailsFurther>
            <Seppp />
            <h5 className="menuitemtitle ttblackt">
              {this.props.titre || ''}
            </h5>
            <p className="menuitemlist ttblack">{this.props.ing1 || ''}</p>
            <p className="menuitemlist ttblack">{this.props.ing2 || ''}</p>
            <p className="menuitemlist ttblack">{this.props.ing3 || ''}</p>
            <p className="menuitemlist ttblack">{this.props.ing4 || ''}</p>
            <p className="menuitemlist ttblack">{this.props.ing5 || ''}</p>
            <p className="menuitemlist ttblack">{this.props.ing6 || ''}</p>
          </SingleItemDetailsFurther>
        </SingleItemDetails>
      </SingleItemDiv>
      <SingleItemDiv2 color={this.props.color}>
        <SingleItemDetails2>
          <SingleItemDetailsFurther2>
            <Seppp />
            <h5 className="menuitemtitle ttwhite">
              {this.props.titre || ''}
            </h5>
            <p className="menuitemlist ttwhite">
									- prix:{' '}
              <span className="pprice">{this.props.prix || '10'}€</span>
            </p>
          </SingleItemDetailsFurther2>
        </SingleItemDetails2>
      </SingleItemDiv2>
    </Holder>
  </div>
		);
	}
}

export default SingleMenuItem;

const media = {
	handheld: (...args) => css`
		@media (max-width: 800px) {
			${css(...args)};
		}
	`,
	handhesmaller: (...args) => css`
		@media (max-width: 300px) {
			${css(...args)};
		}
	`
};

const SingleItemDiv2 = styled.div`
	width: 300px;
	height: 300px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: center;
	position: relative;
	position: absolute;
	background-color: ${props => props.color};
	-webkit-clip-path: circle(12% at 24% 27%);
	clip-path: circle(12% at 24% 27%);
	transition: all ease-out 0.2s;
	cursor: pointer;

	&:hover {
		transition: all ease-out 0.2s;
		-webkit-clip-path: circle(33% at 40% 50%);
		clip-path: circle(33% at 40% 50%);
	}

	${media.handheld`
		width: 250px;
		height: 250px;
		-webkit-clip-path: circle(14% at 16% 23%);
		clip-path: circle(14% at 16% 23%);
  `};

	${media.handhesmaller`
		width: 100%;
		-webkit-clip-path: circle(12% at 24% 27%);
		clip-path: circle(12% at 24% 27%);
  `};
`;

const Holder = styled.div`
	width: 300px;
	height: 300px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: center;
	position: relative;
	position: relative;

	&:hover ${SingleItemDiv2} {
		transition: all ease-out 0.2s;
		-webkit-clip-path: circle(33% at 40% 50%);
		clip-path: circle(33% at 40% 50%);
	}

	${media.handheld`
		width: 250px;
		height: 250px;
  `};

	${media.handhesmaller`
		width: 100%;
  `};
`;

const SingleItemDiv = styled.div`
	width: 300px;
	height: 300px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: center;
	position: relative;

	&:hover ${SingleItemDiv2} {
		transition: all ease-out 0.2s;
		-webkit-clip-path: circle(12% at 20% 20%);
		clip-path: circle(30% at 40% 50%);
	}

	${media.handheld`
		width: 250px;
		height: 250px;
  `};

	${media.handhesmaller`
		width: 100%;
  `};
`;

const SingleItemDetails = styled.div`
	width: 270px;
	height: 270px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: center;
	position: relative;

	${media.handheld`
		width: 220px;
		height: 220px;
  `};

	${media.handhesmaller`
		width: 80%;
  `};
`;

const SingleItemDetailsFurther = styled.div`
	width: 220px;
	height: 220px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: flex-start;
	align-content: flex-start;
	position: relative;

	${media.handheld`
		width: 200px;
		height: 200px;
  `};

	${media.handhesmaller`
		width: 100%;
  `};
`;

const SingleItemDetails2 = styled.div`
	width: 270px;
	height: 270px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: center;
	position: relative;

	${media.handheld`
		width: 220px;
		height: 220px;
  `};

	${media.handhesmaller`
		width: 80%;
  `};
`;

const SingleItemDetailsFurther2 = styled.div`
	width: 220px;
	height: 220px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: flex-start;
	align-content: flex-start;
	position: relative;
	${media.handheld`
		width: 200px;
		height: 200px;
  `};

	${media.handhesmaller`
		width: 100%;
  `};
`;

const Seppp = styled.div`
	width: 100%;
	height: 30px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: flex-start;
	align-content: flex-start;
	position: relative;
`;



