import React from 'react';
import styled, { css } from 'styled-components';

class AnItem extends React.Component {
	render() {
		return (
  <div>
    <Holder>
      <SingleItemDiv>
        <SingleItemDetailsFurther>
          <Seppp />
          <h5 className="menuitemtitle ttblack">
								&#160; &#160; &#160; Nos Suppléments
          </h5>
          <p className="menuitemlist ttblack">
								&#160; &#160; &#160; - Sauce Boursin{' '}
            <span className="darker"> :0.80€</span>
          </p>
          <p className="menuitemlist ttblack">
								&#160; &#160; &#160; - Sauce Chèvre{' '}
            <span className="darker"> : 0.80€</span>
          </p>
          <p className="menuitemlist ttblack">
								&#160; &#160; &#160; - Sauce Poivre{' '}
            <span className="darker"> : 0.80€</span>
          </p>
          <p className="menuitemlist ttblack">
								&#160; &#160; &#160; - Sauce Tomate à la provençale{' '}
            <span className="darker"> : 0.80€</span>
          </p>
        </SingleItemDetailsFurther>
      </SingleItemDiv>
      <SingleItemDiv2 color={this.props.color}>
        <SingleItemDetailsFurther2>
          <Seppp />
          <h5 className="menuitemtitle ttwhite">
								&#160; &#160; &#160; Nos Suppléments
          </h5>
          <p className="menuitemlist ttwhite">
								&#160; &#160; &#160; - Sauce Boursin{' '}
            <span className="darker"> : 0.80€</span>
          </p>
          <p className="menuitemlist ttwhite">
								&#160; &#160; &#160; - Sauce Chèvre{' '}
            <span className="darker"> : 0.80€</span>
          </p>
          <p className="menuitemlist ttwhite">
								&#160; &#160; &#160; - Sauce Poivre{' '}
            <span className="darker"> : 0.80€</span>
          </p>
          <p className="menuitemlist ttwhite">
								&#160; &#160; &#160; - Sauce Tomate à la provençale{' '}
            <span className="darker"> : 0.80€</span>
          </p>
        </SingleItemDetailsFurther2>
      </SingleItemDiv2>
    </Holder>
  </div>
		);
	}
}

export default AnItem;

const media = {
	handhesmaller: (...args) => css`
		@media (max-width: 500px) {
			${css(...args)};
		}
	`
};

const SingleItemDiv2 = styled.div`
	width: 100%;
	height: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: center;
	position: relative;
	position: absolute;
	background-color: ${props => props.color};
	-webkit-clip-path: inset(0% 82% 82% 0%);
	clip-path: inset(0% 82% 72% 0%);
	transition: all ease-out 0.2s;
	cursor: pointer;

	&:hover {
		transition: all ease-out 0.2s;
		-webkit-clip-path: inset(0% 0% 0% 0%);
		clip-path: inset(0% 0% 0% 0%);
	}
`;

const Holder = styled.div`
	width: 400px;
	height: 250px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: center;
	position: relative;
	position: relative;

	&:hover ${SingleItemDiv2} {
		transition: all ease-out 0.2s;
		-webkit-clip-path: inset(15% 22% 22% 12%);
		clip-path: inset(0% 0% 0% 0%);
	}

	${media.handhesmaller`
		width: 100%;
  `};
`;

const SingleItemDiv = styled.div`
	width: 100%;
	height: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: center;
	position: relative;
`;

const SingleItemDetails = styled.div`
	width: 100%;
	height: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: center;
	position: relative;
`;

const SingleItemDetailsFurther = styled.div`
	width: 100%;
	height: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: flex-start;
	align-content: flex-start;
	position: relative;
`;

const SingleItemDetails2 = styled.div`
	width: 100%;
	height: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
	align-content: center;
	position: relative;
`;

const SingleItemDetailsFurther2 = styled.div`
	width: 100%;
	height: 100%;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: flex-start;
	align-content: flex-start;
	position: relative;
`;

const Seppp = styled.div`
	width: 100%;
	height: 30px;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	align-items: flex-start;
	align-content: flex-start;
	position: relative;
`;



